.header__instant-search {
    height: 100%;
    flex-grow: 1;
    margin-right: auto;

    @include minMedia(l) {
        position: relative; }

    &.active {
        .instant-search__form {
            z-index: 20;
            @include minMedia(s) {
                background-color: $primary-yellow-dark; } }

        @include maxMedia(m) {
            .instant-search__close-btn {
                display: block; } }

        @include maxMedia(l) {
            .instant-search__form {
                display: block; }

            .instant-search__show-btn {
                display: none; } }

        @include media(xs-s) {
            .instant-search__form {
                display: flex;
                width: 100vw;
                position: absolute;
                left: 0; } } } }

.instant-search {

    &__form {
        background-color: $primary-yellow;

        @include minMedia(s) {
            position: relative; }

        @include media(xxl) {
            padding: 8px 20px; }

        @include media(l-xxl) {
            padding: 8px 18px; }

        @include media(m-l) {
            padding: 8px 18px; }

        @include media(s-m) {
            padding: 8px 20px; }

        @include media(xs-s) {
            padding: 8px 15px 8px 0; }

        @include maxMedia(l) {
            flex-grow: 1;
            display: none; } }

    &__icon {
        position: absolute;
        top: 21px;
        font-size: $base-fz;
        color: $base-gray;

        @include minMedia(s) {
            left: 35px; }

        @include media(xs-s) {
            left: 70px; } }

    &__clear-btn {
        position: absolute;
        right: 30px;
        top: 19px;
        width: 24px;
        height: 24px;
        font-size: $small-fz;
        color: $base-gray;

        &:hover {
            color: $primary-dark-c; } }

    &__close-btn {
        height: 100%;
        font-size: 24px;
        background-color: $primary-yellow;

        &:hover {
            background-color: $primary-yellow-dark; }

        @include media(m-l) {
            width: 92px;
            left: -92px; }

        @include media(s-m) {
            width: 65px;
            left: -65px;
            line-height: 62px; }

        @include minMedia(s) {
            position: absolute;
            top: 0;
            z-index: 3; }

        @include media(xs-s) {
            width: 62px;
            height: 44px;
            line-height: 46px; }

        @include minMedia(l) {
            display: none; }

        @include maxMedia(l) {
            display: none; } }

    &__show-btn {
        height: 100%;
        font-size: 20px;
        line-height: 64px;

        &:hover {
            background-color: $primary-yellow-dark; }

        @include minMedia(l) {
            display: none; }

        @include maxMedia(l) {
            display: block;
            margin-left: auto; }

        @include media(m-l) {
            width: 62px; }

        @include maxMedia(m) {
            width: 60px; } }

    &__input {
        width: 100%;
        height: 100%;
        padding: 12px 40px 9px 50px;
        font-size: $mini-fz;
        color: $dark-c;
        border: none;
        border-radius: 30px; }

    &__result-block {
        display: none;
        position: absolute;
        top: 60px;
        left: 0;
        right: 0;
        z-index: 20;
        background-color: $light-c;
        box-shadow: $layer-3;

        @include media(l-xxl) {
            min-width: 500px; }

        @include minMedia(l) {
            border-bottom-right-radius: 5px;
            border-bottom-left-radius: 5px; }

        &.visible {
            display: block; } }

    &__all-result-link {
        display: block;
        text-align: right;
        padding: 12px 24px 24px; }

    &__result {

        &.visible {
            display: block; }

        &_empty {

            @include media(xxl) {
                padding: 24px; }

            @include media(l-xxl) {
                padding: 20px; }

            @include media(m-l) {
                padding: 30px; }

            @include media(s-m) {
                padding: 24px; }

            @include media(xs-s) {
                padding: 24px 15px; } } }

    &-empty {
        &__title {
            margin-bottom: 12px;
            font-size: $medium-fz;
            font-family: $bold-f; }

        &__text {
            font-size: $mini-fz;
            line-height: $base-lh; } } }

.instant-search-result {

    &__categories-list {
        margin: 0;
        padding: 12px 0;

        & + .instant-search-result__products-list {
            border-top: 1px solid $border-gray; } }

    &__products-list {
        margin: 0;
        padding: 12px 0; } }

.instant-search {

    &-category {

        &__link {
            display: flex;
            align-items: center;
            justify-content: space-between;
            text-decoration: none;

            @include minMedia(s) {
                font-size: $base-fz; }

            @include media(xs-s) {
                font-size: $mini-fz; }

            @include media(xxl) {
                padding: 0 24px; }

            @include media(l-xxl) {
                padding: 0 20px; }

            @include media(m-l) {
                padding: 0 30px; }

            @include media(s-m) {
                padding: 0 23px; }

            @include media(xs-s) {
                padding: 0 15px; }

            &:hover,
            &.active {
                background-color: $blue; } }

        &__name {
            padding: 4px 0;
            color: $dark-c;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis; }

        &__counter {
            flex-shrink: 0;
            margin-left: 16px;
            color: $base-gray; } }

    &-product {
        display: flex;
        align-items: center;
        padding-top: 12px;
        padding-bottom: 12px;
        color: $dark-c;
        text-decoration: none;

        @include media(xxl) {
            padding-left: 24px;
            padding-right: 24px; }

        @include media(l-xxl) {
            padding-left: 20px;
            padding-right: 20px; }

        @include media(m-l) {
            padding-left: 30px;
            padding-right: 30px; }

        @include media(s-m) {
            padding-left: 23px;
            padding-right: 23px; }

        @include media(xs-s) {
            padding-left: 15px;
            padding-right: 15px; }

        &:hover,
        &.active {
            background-color: $blue; }

        &__img {
            display: block;
            width: 70px;
            height: 70px;
            margin-right: 12px;
            flex-shrink: 0;
            background-color: $light-c; }

        &__info {
            flex-shrink: 1;
            line-height: 1.3;

            @include minMedia(l) {
                max-width: 400px; }

            @include minMedia(s) {
                margin-right: 24px; }

            @include media(xs-s) {
                display: flex;
                flex-wrap: wrap; } }

        &__title {
            display: inline-block;
            font-family: $bold-f;
            text-decoration: none;

            @include minMedia(s) {
                font-size: $base-fz; }

            @include media(xs-s) {
                width: 100%;
                font-size: $mini-fz; } }

        &__model {
            display: block;
            font-size: $small-fz;
            color: $base-gray;

            @include media(xs-s) {
                margin-top: 12px;
                margin-right: auto; } }

        &__price {
            flex-shrink: 0;
            font-family: $bold-f;
            line-height: $micro-lh;

            @include minMedia(s) {
                margin-left: auto;
                font-size: $base-fz; }

            @include media(xs-s) {
                margin-top: 12px;
                font-size: $mini-fz; }

            &_mobile {
                @include minMedia(s) {
                    display: none; } }

            &_desktop {
                @include media(xs-s) {
                    display: none; } } } } }
