header {
    background-color: $primary-blue;
    padding: 15px 0; }

.header-container {
    display: flex;
    align-items: center; }

.header {
    &__logo-link {
        display: block; }

    &__logo-img {
        display: block;
        height: 30px; }

    &__nav {
        margin-left: auto;
        margin-right: 25px; }

    &__weather-widget {}
    &__search-btn {
        display: block;
        width: 43px;
        height: 18px;
        padding-left: 25px;
        color: $light-c;
        font-size: $base-fz; } }

.weather-widget img {
    display: block;
    height: 30px; }
