.last-news {
    padding: 20px;
    border-radius: 4px;
    border: 1px solid #b1c1d2; }

.last-news__section-title {
    margin-bottom: 15px;
    font-size: $big-fz; }

.last-news-list {
    margin: 0;
    padding: 0; }

.last-news-item {

    &:not(:last-of-type) {
        margin-bottom: 20px; }

    &__link {
        display: flex;
        color: $dark-c;
        text-decoration: none;

        &:hover,
        &:focus {
            .last-news-item__title {
                text-decoration: underline; } } }

    &__img {
        margin-right: 20px; }

    &__title {
        font-size: $mini-fz;
        line-height: $mini-lh;
        font-family: $base-f;
        font-weight: normal;
        margin-bottom: 10px; }

    &__date {
        font-size: $micro-fz;
        color: $base-gray;
        text-transform: uppercase; } }
