.page-error {
    margin-bottom: 60px;

    @include minMedia(l) {
        margin-top: 80px; }

    @include maxMedia(l) {
        margin-top: 128px; }

    @include minMedia(m) {
        display: flex;
        align-items: center;
        flex-direction: row; }

    &-outer {
        margin-left: auto;
        margin-right: auto; }

    &_500 {
        margin-top: 150px;
        margin-right: auto;
        margin-left: auto;
        padding: 100px 24px;
        border-radius: 10px;
        border: 1px solid $dark-gray;

        @include media(xxl) {
            width: 1336px; }

        @include media(l-xxl) {
            margin-left: 8.204%;
            margin-right: 8.204%; }

        @include media(m-l) {
            width: 708px; }

        @include media(s-m) {
            width: 522px; }

        @include media(xs-s) {
            margin-left: 15px;
            margin-right: 15px; } }

    &-wrap {
        height: 100%; }

    &__text-wrap {
        @include maxMedia(m) {
            margin-top: 24px;
            text-align: center; } }

    &__img {
        display: block;
        width: 50%;
        flex-shrink: 0;

        @include minMedia(m) {
            margin-right: 24px; }

        @include maxMedia(m) {
            margin-left: auto;
            margin-right: auto; }

        @include media(s-m) {
            width: 360px; }

        @include media(xs-s) {
            width: 100%;
            max-width: 515px; } }

    &__title {
        font-family: $bold-f;

        @include minMedia(m) {
            max-width: 620px;
            margin-bottom: 36px; }

        @include maxMedia(m) {
            margin-bottom: 24px; }

        @include media(xxl) {
            font-size: $huge-fz; }

        @include media(l-xxl) {
            font-size: $bigger-fz; }

        @include maxMedia(l) {
            font-size: $big-fz; } }

    &__btn {
        display: inline-block; } }

.maintenance-mode {
    display: flex;
    min-height: 100vh;
    padding: 15px;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    background-image: url(../images/maintenance-mode.jpg);
    background-color: $dark-c;
    background-size: cover;
    background-position: center center;
    color: $light-c;

    &__logo {
        display: block;
        margin-left: auto;
        margin-right: auto;

        @include minMedia(m) {
            margin-bottom: 48px; }

        @include maxMedia(m) {
            margin-bottom: 24px; }

        @include media(xxl) {
            width: 380px; }

        @include media(l-xxl) {
            width: 20vw; }

        @include media(m-l) {
            width: 200px; }

        @include media(s-m) {
            width: 140px; }

        @include media(xs-s) {
            width: 140px; } }

    &__title {
        font-family: $bold-f;

        @include minMedia(l) {
            font-size: $huge-fz; }

        @include media(m-l) {
            font-size: $bigger-fz; }

        @include minMedia(m) {
            margin-bottom: 24px; }

        @include maxMedia(m) {
            margin-bottom: 12px;
            font-size: $big-fz; } }

    &__text-wrap {
        max-width: 500px;
        margin: 0 auto;
        color: $light-c; }

    .wysiwyg-container {
        color: $light-c; } }
