.wysiwyg-container {
    line-height: $base-lh;
    color: $dark-c;

    .left {
        text-align: left; }

    .right {
        text-align: right; }

    .center {
        text-align: center; }

    [class*="wysiwyg-h"] {
        font-family: $bold-f !important;
        line-height: $mini-lh !important;

        @include minMedia(m) {
            margin-top: 48px !important;
            margin-bottom: 24px !important; }

        @include maxMedia(m) {
            margin-top: 36px !important;
            margin-bottom: 16px !important; }

        &.wysiwyg-h1 {
            @include minMedia(m) {
                font-size: $h1-fz !important; }

            @include maxMedia(m) {
                font-size: $h3-fz !important; } }

        &.wysiwyg-h2 {
            @include minMedia(m) {
                font-size: $h2-fz !important; }

            @include maxMedia(m) {
                font-size: $h4-fz !important; } }

        &.wysiwyg-h3 {
            @include minMedia(m) {
                font-size: $h3-fz !important; }

            @include maxMedia(m) {
                font-size: $h5-fz !important; } }

        &:first-child {
            margin-top: 0 !important; }

        &:last-child {
            margin-bottom: 0 !important; } }

    b,
    strong {
        font-family: $bold-f !important;
        font-weight: bold; }

    i {
        font-style: italic; }

    a,
    a:link,
    a:visited {
        color: $primary-blue;
        text-decoration: none;

        &:hover {
            text-decoration: underline; } }

    p {
        margin: 0 !important;
        font-size: $base-fz;

        @include minMedia(m) {
            margin-bottom: 24px !important; }

        @include maxMedia(m) {
            margin-bottom: 16px !important; }

        &:last-child {
            margin-bottom: 0 !important; } }

    ul,
    ol {
        padding-left: 0 !important;
        font-size: $base-fz !important;
        line-height: $base-lh !important;

        @include minMedia(m) {
            margin: 24px 0 36px 36px !important; }

        @include maxMedia(m) {
            margin: 16px 0 24px 24px !important; }

        li {
            margin-top: 8px !important;
            padding-left: 12px !important;

            &:first-child {
                margin-top: 0 !important; } }

        ul,
        ol {
            @include minMedia(m) {
                margin: 24px 0 24px 24px !important; }

            @include maxMedia(m) {
                margin: 16px 0 16px 16px !important; } } }

    & > ul,
    & > ol {
        &:first-child {
            margin-top: 0 !important; }

        &:last-child {
            margin-bottom: 0 !important; } }

    ul {
        li {
            list-style: disc !important; } }

    ol {
        li {
            list-style: decimal !important; } } }

.wysiwyg-container_small {

    [class*="wysiwyg-h"] {

        &.wysiwyg-h1 {
            margin-top: 16px !important;
            margin-bottom: 12px !important;
            font-size: $base-fz !important; }

        &.wysiwyg-h2 {
            margin-top: 16px !important;
            margin-bottom: 16px !important;
            font-size: $mini-fz !important; }

        &.wysiwyg-h3 {
            margin-top: 16px !important;
            margin-bottom: 24px !important;
            font-size: $small-fz !important; } }

    p {
        margin-bottom: 16px !important;
        font-size: $mini-fz; }

    ol {
        margin: 1em 0 1em 2em !important;
        padding-left: 0 !important;
        font-size: $mini-fz !important;

        ul,
        ol {
            margin: 16px 0 16px 8px !important;

            &:first-child {
                margin: 16px 0 16px 8px !important; } } } }
